export default {
    'url': '/terminalSecurity/intranetStatistics/:type/:limit/:currentPage/:troopsId',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {
            'detail|8': [
                {
                    'IP': '@ip',
                    'hostname': '@virusNames',
                    'username': '@cname',
                    'company': '单位名称',
                    'time': '@datetime'
                }
            ]
        }
    }
 }


