export default {
    'url': '/terminalSecurity/internetVirus/virusTable/:type/:limit/:currentPage/:troopsId',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {        
            'list|5': [
                {
                    'name': '@virusNames',
                    'id': '@id',
                    'count|1000-9999': 200,
                    'defeatRate': '0.1',
                    'intro': '病毒简介'
                }
            ]
        }
    }
 }


