 import $ from 'jquery'
 import './index.css'
 import hbs from './index.hbs'
 import { fetchInterval } from '@/util/request'
 import ComTable from '../comTabel'

 let comTable = new ComTable()

 export default class intranetStatistics {
    render(type, troopsId) {
        fetchInterval('fetchIntranetStatistics', {type, troopsId}, data => {
            $('.statistics-box').empty().append(hbs(data))
            this.addEvent(type, troopsId)
        }, 600000)
    }

    /**
     * 事件添加
     */
    addEvent(type, troopsId){
        // 点击终端设备调用相应弹窗
        let tcHeaders = ['主机IP', '主机名称', '用户名称', '单位名称', '注册时间']
        const btn = $('.base-box')
        btn.on('click', e => {
            const limit = 8
            const currentPage = 0
            let typeData = {type, limit, currentPage, troopsId}
            comTable.bindClickEvent(typeData, 'fetchIntranetStatisticsList', tcHeaders)
        })
        
    }
 }


