 import $ from 'jquery'
 import './index.css'
 import hbs from './index.hbs'
 import { fetchInterval } from '@/util/request'
 import ComTable from '../comTabel'

 let comTable = new ComTable()
 export default class intranetViolation {
     render(type, troopsId) {
        fetchInterval('fetchIntranetViolation', { type, troopsId }, data => {
            $('.violation-box').empty().append(hbs(data.list))
            this.addEvent(type, troopsId)
        }, 600000)
     }

     /**
      * 添加事件
      */
     addEvent(type, troopsId){
        // 点击终端设备调用相应弹窗
        let tcHeaders = []
        const btn = $('.violation-list li')
        btn.on('click', e => {
            const id = $(e.currentTarget).index()
            const limit = 8
            const currentPage = 0
            let typeData = {type, id, limit, currentPage, troopsId}
            console.log(id === 2)
            if(id === 0 || id === 1 ) {
                tcHeaders = ['用户名称', '单位名称', '违规外联时间', 'IP地址']
            } else {
                tcHeaders = ['用户名称', '单位名称', '违规外联时间', 'IP地址', '操作类型', '报警类型']
            }
            comTable.bindClickEvent( typeData, 'fetchIntranetViolationList', tcHeaders)
        })
        
     }
 }


