export default {
    'url': '/terminalSecurity/internetBug/bugTable/:type/:limit/:currentPage/:troopsId',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {        
            'list|5': [
                {
                    'name': '@virusNames',
                    'id': '@id',
                    'count|100-999': 120,
                    'fixedCount|100-999': 123
                }
            ]
        }
    }
 }


