export default {
    'url': '/terminalSecurity/intranetVirus/infecTable/:type/:troopsId',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {        
            'infect|5': [
                {
                    'name|+1': ['一支队','二支队','一十三支队','四支队','一十五支队'],
                    'value|1000-9999': 1000
                }
            ],
            'resolve|5': [
                {
                    'name|+1': ['一支队','二支队','一十三支队','四支队','一十五支队'],
                    'value|1000-9999': 1000
                }
            ]
        }
    }
 }


