import $ from 'jquery'
import './index.css'
import hbs from './index.hbs'
import { fetchInterval } from '@/util/request'
import ComTable from '../comTabel'

let comTable = new ComTable()
export default class intranetViolation {
    render(type, troopsId) {
        fetchInterval('fetchIntranetVirus', {type, troopsId}, data => {
            $('.intra-virus-box').empty().append(hbs(data.list))
            // 点击终端设备调用相应弹窗
            let tcHeaders = ['主机IP', '病毒名称', '病毒类型', '处置结果', '用户名称', '单位']
            const btn = $('.virus-box > div')
            btn.on('click', e => {
                const id = $(e.currentTarget).index()
                const limit = 8
                const currentPage = 0
                let typeData = {type, id, limit, currentPage, troopsId}
                comTable.bindClickEvent(typeData, 'fetchIntranetVirusList', tcHeaders)
            })
            
        }, 600000)
    }
}


