export default {
    'url': '/terminalSecurity/intranetViolation/:type/:id/:limit/:currentPage/:troopsId',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {
            'detail|8': [
                {
                    'username': '@cname',
                    'company': '@cname',
                    'time': '@datetime',
                    'IP': '@ip'
                }
            ]
        }
    }
 }


