import $ from 'jquery'
import './index.css'
import hbs from './index.hbs'
import hbsTab from './table.hbs'
import hbsTab1 from './table1.hbs'
import { fetch, fetchInterval } from '@/util/request'
import { ModalServer } from '@/components'
import { deformationToOrigin } from '@/util/zoom/zoom'
 
import ComTable from '../comTabel'
let comTable = new ComTable()
export default class InternetBug{
    /**
     * 添加点击事件
     * @param {string} type 当前时间粒度 
     */
    addEvent(type, troopsId) {
      // 浮窗
      $('#interbugTable .mouse-hover')
        .on('mousemove', ev => {
          let Ev = ev || window.event
          let pos = deformationToOrigin([Ev.pageX , Ev.pageY])
          $('.com-floating .value').html($(Ev.currentTarget).attr('data-title'))
          $('.com-floating').stop(true, true).fadeIn(500)
          $('.com-floating').css({'transform': `translate(${pos[0] + 30}px, ${pos[1] + 30}px)`})
        }).on('mouseout', () => {
          $('.com-floating .value').empty()
          $('.com-floating').stop(true, true).fadeOut(500)
        })
      // 点击漏洞数/打补丁数调用相应弹窗
      let tcHeaders = ['用户名称', '单位名称', '主机IP', '漏洞名称', '修复状态', '发现时间']
      const btn = $('.internet-bug-box .but-base')
      btn.on('click', e => {
          const id = $(e.currentTarget).index()
          const limit = 8
          const currentPage = 0
          let typeData = {type, id, limit, currentPage, troopsId}
          comTable.bindClickEvent(typeData, 'fetchInternetBugList', tcHeaders)
      })

      // 点击表格中的查看调用相应弹窗
      let tcHeadersTab = ['漏洞名称', '用户姓名', '单位名称', 'IP地址', '修复状态', '发现时间']
      const btnTab = $('.internet-bug-box .attachment')
      btnTab.on('click', e => {
          const id = $(e.currentTarget).attr('data-id')
          const limit = 8
          const currentPage = 0
          let typeData = {type, id, limit, currentPage, troopsId}
          comTable.bindClickEvent(typeData, 'fetchInternetBugTabList', tcHeadersTab)
      })

      // 点击查看全部，掉相应的弹框
      let tcHeadersAll = ['漏洞名称', '涉及终端数量', '修复数量', '感染范围']
      const btnAll = $('.internet-bug-box .but-all')
      btnAll.on('click', () => {
        const limit = 8
        const currentPage = 0
        let typeData = {type, limit, currentPage, troopsId}
        this.bindClickEvent(typeData, 'fetchInternetBugTab', tcHeadersAll)
      })
    }

  /**
   * 绑定按钮的点击事件，呼出表格弹窗(一级弹窗)
   * @param  {Object} btn   按钮
   * @param  {String} type  按钮标识
   * @param  {String} type  接口调用地址名称
   * @param  {object} type  表头名称
   * @return {void}   void
   */
  bindClickEvent(typeData, urls, headers) {
    const url = urls
    // 点击时初始化当前页码
    // typeData.currentPage = 0
    // fetch(url, typeData, dataSource => {
      const modal = new ModalServer({
        headers: headers,
        // 点击上一页
        onPrev: () => {
          if (typeData.currentPage > 0) {
            typeData.currentPage -= 1
            fetch(url, typeData, dataTab => {
              // 这里删除了一个固定属性，原因是后端说他们那边没法处理，然后就我们处理了一下。
              dataTab.list.map(d => {
                delete d['ROW_ID']
              })
              $('#modalTbody .row').remove()
              $('#modalTbody').append(hbsTab1({dataTab}))
              this.allList(typeData)
            })
          } else {
            alert('这是第一页!')
          }
        },
        // 点击下一页
        onNext: () => {
          typeData.currentPage += 1
          fetch(url, typeData, dataTab => {
            if (dataTab.list.length > 0) {
              // 这里删除了一个固定属性，原因是后端说他们那边没法处理，然后就我们处理了一下。
              dataTab.list.map(d => {
                delete d['ROW_ID']
              })
              $('#modalTbody .row').remove()
              $('#modalTbody').append(hbsTab1({dataTab}))
              this.allList(typeData)
            } else {
              typeData.currentPage -= 1
              alert('这是最后一页!')
            }
          })
        }
      })
      modal.init()
      fetch('fetchInternetBugTab', typeData, dataTab => {
        // 这里删除了一个固定属性，原因是后端说他们那边没法处理，然后就我们处理了一下。
        dataTab.list.map(d => {
          delete d['ROW_ID']
        })
        $('#modalTbody').append(hbsTab1({dataTab}))
        // 没数据时添加暂无数据
        if(dataTab.list.length != 0) {
          $('.com-hint').remove()
        }
        this.allList(typeData)
      })
      
    // })
  }

  /**
   * 查看详情后的二级弹窗
   */
  allList(typeDatas) {
    $('main').off().on('click', '#modalTbody .attachment', e => {
      // 点击表格中的查看调用相应弹窗
      let tcHeadersTab = ['漏洞名称', '用户姓名', '单位名称', 'IP地址', '修复状态','发现时间']
      const id = $(e.currentTarget).attr('data-id')
      const limit = 8
      const currentPage = 0
      let typeData = {type: typeDatas.type , id, limit, currentPage, troopsId: typeDatas.troopsId}
      $('#modalContainer').remove()
      comTable.bindClickEvent(typeData, 'fetchInternetBugTabList', tcHeadersTab)
      $('#modalReturn').stop().show()
    })
    $('#modalReturn').off().on('click', () => {
      let tcHeadersAll = ['漏洞名称', '涉及终端数量', '修复数量', '感染范围']
      $('#modalContainer').remove()
      $('#modalReturn').stop().hide()
      this.bindClickEvent(typeDatas, 'fetchInternetBugTab', tcHeadersAll)
    })
  }

    /**
     * 初始渲染
     * @param {string} type 当前时间粒度 
     */
    render(type, troopsId) {
      fetchInterval('fetchInternetBug', {type, troopsId}, data => {
        fetch('fetchInternetBugTab', {type, limit: 5, currentPage: 0, troopsId}, dataTab => {
          let headers = ['漏洞名称', '涉及终端数量', '修复数量', '感染范围']
          $('.internet-bug-box').empty().append(hbs({data, headers}))
          if(dataTab.list.length === 0) {
            $('#interbugTable').append('<span class="com-hint">暂无数据</span>')
          } else {
            $('#interbugTable tbody').empty().append(hbsTab({dataTab}))
          }
           this.addEvent(type, troopsId)
        })
      }, 600000)
    }
 }


