import './index.css'
import $ from 'jquery'
import echarts from 'echarts'
import hbs from './index.hbs'
import barOpt from './barOpt'
import { fetchInterval } from '@/util/request'

export default class intranetVirusInfecTable{
    render(type, troopsId) {
      $('#virusInfecTable').empty().append(hbs)
      // 渲染柱形图
      let chart = echarts.init(document.getElementById('serverSecurityChart'))
      fetchInterval('fetchIntranetVirusInfecTab', {type, troopsId}, data => {
        if(!$.isEmptyObject(data)) {
          let newData = this.disposeData(data)
          chart.setOption(barOpt(newData, newData.map(d => d.name)))
        } else {
          $('#virusInfecTable').append('<span class="com-hint">暂无数据</span>')
        }
      }, 600000)
    }

    /**
     * 转换数据集
     * @param {object} data 要处理的数据集
     */
    disposeData(data) {
      let newData = []
      data.infect.map( (d, i) => {
        newData.push({
          name: d.name,
          value: [d.value, data.resolve[i].value]
        })
      })
      return newData
    }
}


