export default {
    'url': '/terminalSecurity/intranetVirus/virusTable/:type/:troopsId',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {        
            'list|5': [
                {
                    'name': '@cname',
                    'id': '@id',
                    'count|1000-9999': 200,
                    'defeatRate': '0.1',
                    'intro': '病毒简介',
                }
            ]
        }
    }
 }



