 import $ from 'jquery'
 import './index.css'
 import hbs from './index.hbs'
 import { fetch, fetchInterval } from '@/util/request'
 import ComTable from '../comTabel'

 let comTable = new ComTable()
export default class InternetStatistics {
    render(type, troopsId) {
        fetchInterval('fetchInternetStatistics', {type, troopsId}, data => {
            $('.inter-statistics-box').empty().append(hbs(data.list))
            this.addEvent(type, troopsId)
        }, 600000)
    }

    /**
     * 点击各选项弹出弹框列表
     * @param {*} type  当前时间
     * @param {*} troopsId  用户区域id，主要是用于做权限
     */
    addEvent(type, troopsId) {
        // 点击终端设备调用相应弹窗
        let tcHeaders = []
        const btn = $('.inter-statistics-box > div')
        btn.on('click', e => {
            const id = $(e.currentTarget).index()
            const limit = 8
            const currentPage = 0
            let typeData = {type, id, limit, currentPage, troopsId}
            if(id === 0) {
                tcHeaders = ['用户名称', '单位名称','IP地址', '主机名称','注册时间' ]
            } else {
                tcHeaders = ['用户名称', '单位名称', 'IP地址', '违规时间', 'U盘ID', '操作类型', '告警类型']
            }
            comTable.bindClickEvent( typeData, 'fetchInternetStatisticsList', tcHeaders)
        })
    }
}



