import echarts from 'echarts'

const bar = (name, data, colors) => ({
  name,
  type: 'bar',
  data,
  barWidth: 20,
  barMinHeight: 10,
  barGap: '100%',
  itemStyle: {
    color: new echarts.graphic.LinearGradient(
      0, 0, 0, 1,
      [
        { offset: 0, color: colors[0] },
        { offset: 0.5, color: colors[1] },
        { offset: 1, color: colors[2] }
      ]
    ),
    barBorderRadius: [20, 20]
  }
})

export default (data, domain) => ({
  title: {
    show: false
  },
  tooltip: {
    trigger: 'axis',
    textStyle: {
      fontSize: 26
    }
  },
  legend: {
    data: [
      '感染数量',
      '处置数量'
    ],
    right: '6%',
    inactiveColor: '#4f4f4f',
    itemGap: 40,
    textStyle: {
      fontSize: 24,
      color: '#FFF'
    }
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  xAxis: {
    type: 'category',
    axisLine: {
      lineStyle: {
        color: '#424e77'
      }
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      color: '#a4d5ff',
      fontSize: 24,
      interval:0,
      rotate:40
    },
    data: domain
  },
  yAxis: {
    type: 'value',
    axisLine: {
      show: false
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      fontSize: 26,
      color: '#35e6fc'
    },
    splitLine: {
      lineStyle: {
        width: 1,
        color: '#424e77'
      }
    }
  },
  series: [
    bar('感染数量', data.map(d => d.value[0]),
      ['rgba(248, 89, 4, 1)', 'rgba(1, 46, 122, 1)', 'rgba(1, 46, 122, 1)', 'rgba(1, 23, 91, 1)', 'rgba(1, 23, 91, 1)']),
    bar('处置数量', data.map(d => d.value[1]),
    ['rgba(247, 204, 4, 1)', 'rgba(1, 46, 122, 1)', 'rgba(1, 46, 122, 1)', 'rgba(1, 23, 91, 1)', 'rgba(1, 23, 91, 1)'])
  ]
})



