// import $ from 'jquery'
import $ from 'jquery'
import './index.css'
import loader from '@/loader/loader'
import apis from './mock/index'
import { fetch } from '@/util/request'

import LoadNav from '@/components/loadNav/index'

let loadNav = new LoadNav()

import {
    Header,
    Frame,
    Times,
    IntranetStatistics,
    IntranetViolation,
    IntranetVirus,
    IntranetVirusTable,
    IntranetVirusInfecTable,
    InternetStatistics,
    InternetVirus,
    InternetBug
  } from './components'

// 组件
const header = new Header()
const frame = new Frame()
const times = new Times()
// 内网终端
const intranetStatistics = new IntranetStatistics()
const intranetViolation = new IntranetViolation()
const intranetVirus = new IntranetVirus()
const intranetVirusTable = new IntranetVirusTable()
const intranetVirusInfecTable = new IntranetVirusInfecTable()
// 互联网终端
const internetStatistics = new InternetStatistics()
const internetVirus = new InternetVirus()
const internetBug = new InternetBug()

let comType = 'days'
let comTroopsId = ''
loader.load({
    apis,
    init() {
        loadNav.init() //  页面导航
        $('.side-l-nav li:eq(1)').addClass('active')
        // 判断是否有用户登录
        fetch('fetchLoginUsername', data => {
            if(data.name != undefined) {
                fetch('fetchTroopsId', d => {
                    comTroopsId = d.troopsId
                    header.render(data)
                    frame.render()
                    times.render()
                    this.changeRender(comType, d.troopsId)
                    this.addEvent()
                })
                
            } else {
                alert('请先登录')
                fetch('fetchLogOut', data => {
                    $(window).attr('location', data.url)
                })
            }
        })

    },
    
    /**
     * 需要根据时间粒度改变的组件
     * @param {string} comType 当前选中的时间粒度
     */
    changeRender(comType, troopsId){
        intranetStatistics.render(comType, troopsId)
        intranetViolation.render(comType, troopsId)
        intranetVirus.render(comType, troopsId)
        intranetVirusTable.render(comType, troopsId)
        intranetVirusInfecTable.render(comType, troopsId)
        internetStatistics.render(comType, troopsId)
        internetVirus.render(comType, troopsId)
        internetBug.render(comType, troopsId)
    },

    /**
     * 添加事件
     */
    addEvent() {
        $('.time-main').on('click', 'span', (e) => {
            $(e.currentTarget).siblings().removeClass('active')
            $(e.currentTarget).addClass('active')
            let activeTime = $(e.currentTarget).attr('data-type')
            if(comType != activeTime) {
                comType = activeTime
                this.changeRender(comType, comTroopsId)
            }
        })

        // 点击退出
        $('.user-box').on('click', '.log-out', () => {
            fetch('fetchLogOut', data => {
                $(window).attr('location', data.url)
            })
        })
    }
  }, 3360, 1890)



